import { faEnvelope } from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useEffect } from "react";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import "react-datepicker/dist/react-datepicker.css";
import { invokeDummyFunction } from "../../services/crmDataService";
import { ZOHO } from "../../vendor/ZSDK";
import "./header.css";
const CustomInput = React.forwardRef((props, ref) => {
  return (
    <div>
      <label onClick={props.onClick} ref={ref}>
        {props.value || props.placeholder}
      </label>
    </div>
  );
});

const Header = ({
  title,
  setZohoID,
  closingDate = null,
}) => {
  const [ClosingDate, setClosingDate] = React.useState(new Date());
  const [showDatePicker, setShowDatePicker] = React.useState(false);
  const [headerDetails, setHeaderDetails] = React.useState({});

  useEffect(() => {
    async function initializeSDK() {
      let details;

      ZOHO.embeddedApp.on("PageLoad", async function (data) {
        ZOHO.CRM.UI.Resize({ width: "1000", height: "720" });
        details = await invokeDummyFunction(data.EntityId[0]);
        setZohoID(data.EntityId[0]);
        //convert the closing_date in the format of MM/dd/yyyy
        let headerDetails = JSON.parse(details?.details?.output);

        headerDetails.closing_date = new Date(
          headerDetails.closing_date
        ).toLocaleDateString("en-US");
        setHeaderDetails(headerDetails);
      });

      ZOHO.embeddedApp.init().then(() => {
      });
    }

    initializeSDK();
  }, []);

  useEffect(() => {

    if(headerDetails){
        let dateArray = headerDetails?.closing_date?.split("-");
        let date;
        if (dateArray) {
          date = new Date(dateArray[0], dateArray[1] - 1, dateArray[2]);
          setClosingDate(new Date(date));
        } 

        
    }
  }, [headerDetails]);


  return (
    <>
      <Container fluid>
        <Row>
          <Col>
            <img
              src={require("./CHR.png")}
              alt="CHR Logo"
              width="150"
              height="40"
              className="logo"
            />
            <h4 className="title">{title}</h4>

            {closingDate ? (
              <span
                className="align-self-baseline d-block"
                onClick={() => setShowDatePicker(!showDatePicker)}
              >
                <h6 className="title">
                  <b>Contract:</b>&nbsp;{headerDetails.contract_yr} |{" "}
                  <b>&nbsp;Closing Date:&nbsp; </b>
                  {headerDetails.closing_date}
                </h6>
                {""}
              </span>
            ) : (
              <h6 className="title">
                <b>Contract:</b> {headerDetails.contract_yr}
              </h6>
            )}
          </Col>
          <Col>
            <h6 className="right-side">{new Date().toLocaleDateString('en-US')}</h6>
            <h6 className="right-side">
              <b>Transaction Manager:</b>
            </h6>
            <h6 className="right-side">
              <p>
                {headerDetails.tm_name} &nbsp;
                <a href={`mailto:` + headerDetails.tm_email}>
                  <FontAwesomeIcon icon={faEnvelope} color="#1678f7" />
                </a>
              </p>
            </h6>
            <h6 className="right-side">{headerDetails.tm_phone}</h6>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Header;
