import { useEffect, useState } from 'react';
import { Modal } from 'react-bootstrap';

export default function ConfirmCompleteGroupModal({show, data, handleClose, confirm}) {

    const [selectedGroup, setSelectedGroup] = useState([]);
    
    useEffect(() => {
        data?.map((record) => {
           record.completeDateLocal = true;
            return record;
        })
        setSelectedGroup(data);
    }, [data])

    function handleSaveModal(){
        confirm(selectedGroup);
    }

    function setChecked(id, event){
        let newData = selectedGroup.map((item) => {
            if(item.id === id){
                item.completeDateLocal = event.target.checked;
            }
            return item;
        })
        setSelectedGroup(newData);
    }
    
    return  (
        <Modal
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        show={show}
        onHide={() => {
            handleClose();
        }}
        >
            <Modal.Header>
                <Modal.Title>Are you sure you want to complete this group?</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <table
                    className="table table-striped table-bordered table-hover"
                    id="dataTables-example"
                >
                    <thead>
                        <tr>
                            <th>Complete</th>
                            <th>Group</th>
                            <th>Key Date</th>
                            <th>Deadline</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            data.map((item, index) => (
                                <tr key={index}>
                                    <td>
                                        <input type="checkbox" disabled={item.complete_date} defaultChecked onChange={(e) => setChecked(item.id, e)}/>
                                    </td>
                                    <td>{item.complete_group}</td>
                                    <td>{item.key_date}</td>
                                    <td>{ item.deadline ? new Date(item.deadline).toLocaleDateString('en-US') : ""}</td>
                                </tr>   
                            ))
                        }
                    </tbody>
                </table>
            </Modal.Body>
            <Modal.Footer>
        <button
            className="btn btn-primary"
            onClick={() => {
            handleClose();
            }}
        >
            Cancel
        </button>
        <button
            className="btn btn-primary"
            onClick={() => {
            handleSaveModal();
            }}
        >
            Confirm
        </button>
        </Modal.Footer>
        

        </Modal>
    )
}