import { css } from "@emotion/react";
import React, { useEffect } from "react";
import Modal from "react-bootstrap/Modal";
import { getLastNotes } from "../../services/crmDataService";
import "../table/table.css";
const styles = {
  ntoeCard: css`
    border: 1px solid black;
    padding: 10px;
    margin-bottom: 10px;
  `,
};
function NotesListModal({ show, recordID, handleClose, notes }) {

  const [notesList, setNotesList] = React.useState([]);
  const [showLoader, setShowLoader] = React.useState(false);
  
  async function getLastNote() {
    setShowLoader(true);
    let data = {
      id: recordID,
    };
    await getLastNotes(JSON.stringify(data)).then((data) => {
      //put the data.details.output in a array and set it to notesList
      
      let parsedNotes = JSON.parse(data?.details.output).Notes;
      setNotesList(parsedNotes);
      setShowLoader(false);
    });
  }

  useEffect(() => {    
    if (recordID !== null && recordID !== undefined && show) {
      getLastNote();
    }

  }, [show])

  return (
    <>
      {/* {showLoader && (
        <div id="loader" className="loader-position opacity">
          <div className="loader"></div>
        </div>
      )} */}
      <Modal
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        show={show}
        onHide={() => {
          handleClose();
        }}
      >
        {showLoader && (
            <div id="loader" style={{background: "black"}} className="loader-position opacity">
              <div className="loader"></div>
              </div>
              )}
        <Modal.Header closeButton>
          <Modal.Title>Notes</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          
          {!showLoader && notesList?.map((note, index) => {
            return (
              <div key={index} style={styles.ntoeCard}>
                <b>{note?.Note_O_D_T}</b>
                <p>{note?.Note_Content}</p>
              </div>
            );
          })}
        </Modal.Body>
      </Modal>
    </>
  );
}

export default React.memo(NotesListModal);
