import { faCircleDown, faPlusSquare } from "@fortawesome/free-regular-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useEffect } from "react";
import { FormCheck, Table } from "react-bootstrap";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { closeWidget, closeWidgetOnSave, completeRecord, getTableRecords, postRecord, updateLastNote } from "../../services/crmDataService";
import ConfirmCompleteGroupModal from "../CompleteGroupModal";
import ConfirmCompleteModal from "../confirmationModal";
import Footer from "../Footer";
import NotesListModal from "../NotesListModal";
import NotesModal from "../notesModal";
import "./table.css";

const DynamicTable = ({ zohoId }) => {
  const [tableData, setTableData] = React.useState([]);
  const [show, setShow] = React.useState(false);
  const [showListModal, setShowListModal] = React.useState(false);
  const [selectedRow, setSelectedRow] = React.useState(null);
  const [showLoader, setShowLoader] = React.useState(false);
  const [showConfirmModal, setShowConfirmModal] = React.useState(false);
  const [openGroupModal, setOpenGroupModal] = React.useState(false);
  const [selectedGroup, setSelectedGroup] = React.useState([]);

  useEffect(() => {
    async function intiTable() {
      if (zohoId) {
        setShowLoader(true);
        await getTableRecords(zohoId).then(async (data) => {
          console.log(JSON.parse(data?.details.output).table_data);
          let transTableData = JSON.parse(data?.details.output).table_data;
          transTableData.forEach((row, index) => {
            row.completeDate = false;
            row.notes = [];
            if(row.deadline) {
              row.hasDeadline = true;
            }
            row.note = "";
            if(row.deadline) {
              let dateArray = row?.deadline.split("-");
              row.deadline =  new Date(dateArray[0], dateArray[1] - 1, dateArray[2]);  
          }
          });
          setTableData(transTableData);
          setShowLoader(false);
        });
      }
    }
    intiTable();
  }, [zohoId]);



  function handleModalVisibility(id) {
    setShow(true);
    //find the object in the array and set it to selectedRowgg
    setSelectedRow(
      tableData.find((item) => {
        // console.log("the item is :", item);
        return item.id === id;
      })
    );
  }

  function handleListModalVisibility(id) {
    setShowListModal(true);
    //find the object in the array and set it to selectedRowgg
    setSelectedRow(
      tableData.find((item) => {
        // console.log("the item is :", item);
        return item.id === id;
      })
    );
  }

  async function handleDeadline(date, index) {
    //find the row on the base of index and update the deadline
    const updatedTableData = tableData.map((row, i) => {
      if (i === index) {
        //convert the date into locale string in the form of yyyy-mm-dd
        row.deadline = date;
      }
      return row;
    });
    setTableData(updatedTableData);
  }

  async function handleNotes(value) {

    setShowLoader(true);
    let data = {
      id: selectedRow.id,
      note: value
    }
    await updateLastNote(JSON.stringify(data)).then(async (data) => {
      setShowLoader(false)
    });
    
    const updatedTableData = tableData.map((row) => {
      if (row.id === selectedRow.id) { 
        row.notes.push(value);
        row.note = "";
      }
      return row;
    });
    setTableData(updatedTableData);
  }

  async function handleSubmit (){
    //convert the date as per the format required by zoho
    let data = tableData.map((row) => {
      if(row.deadline) {
        row.deadline = row.deadline.toLocaleDateString("en-CA");
      }
      return row;
    });
    //extrct only the id and deadline from the tableData
    data = data.map(({ id, deadline }) => ({ id, deadline }));

    await postRecord(JSON.stringify(data)).then(async (data) => {
      await closeOnSave();
    });
  }  
  
  async function closeWithoutSave () {
    await closeWidget();
  }

  async function closeOnSave() {
    await closeWidgetOnSave ();
  }

  async function handleComplete(){
    setShowLoader(true)
    //updating the table data to make the row disabled at frontend
    const updatedTableData = tableData.map((row) => {
      if (row.id === selectedRow.id) {
        row.completeDate = !row.completeDate;
      }
      return row;
    });
    //hiding moda and preparing data to send at backend
    setShowConfirmModal(false);
    selectedRow.complete_date = true;
    let data = [{
      id: selectedRow.id,
      complete_date: selectedRow.complete_date
    }]
    //calling the backend api to update
    await completeRecord(JSON.stringify(data)).then(() => {
      setShowLoader(false)
    })
    //setting the table data with updated record
    setTableData(updatedTableData);
  }

  function handleCompleteChange(index){
    setShowConfirmModal(true);
    setSelectedRow(
      tableData.find((item) => { 
        return item.id === index;
      }
    ));
  }
  function handleCompleteGroupChange(item_name){
    setOpenGroupModal(true);
    //select all the row which has the same item name as the selected row
    let selectedRows = tableData.filter((item) => {
      return item.complete_group === item_name;
    });
    setSelectedGroup(selectedRows);
  }

  async function handleCompleteGroupConfirmation(data) {
    // setShowLoader(true);
    //merge table data with the data received from the modal
    const updatedTableData = tableData.map((row) => {
      data.forEach((record) => {
        if (row.id === record.id) {
          row.complete_date = record.completeDateLocal;
        }
      })
      return row;
    });

    let filteredRecords = data.filter((record) => {
      return record.complete_date === true;
    })

    const dataToSend = filteredRecords.map((record) => {
      
      record = {id: record.id, complete_date: record.complete_date }
      return record
    });
    await completeRecord(JSON.stringify(dataToSend)).then(() => {
      setShowLoader(false)
    })
    setTableData(updatedTableData);
    setOpenGroupModal(false);
  }
  return (
    <>
      {showLoader && <div id="loader" className="loader-position opacity">
      
        <div className="loader"></div>
      </div>}
      {<Table striped bordered hover className="m-5 p-5 table-style">
        <thead>
          <tr>
            <th>Complete</th>
            <th>Group</th>
            <th>Key Date</th>
            <th>Deadline</th>
            <th>Last Note</th>
            <th>Add Note</th>
          </tr>
        </thead>
        <tbody>
          {tableData?.map((item, index) => (
            <tr key={index}>
              <td>
                <FormCheck
                  type="checkbox"
                  checked={item.complete_date}
                  onChange={() => handleCompleteChange(item.id)}
                  disabled={item.complete_date}
                />
              </td>
              <td className="hover" onClick={() => handleCompleteGroupChange(item.complete_group)}>{item.complete_group}</td>
              <td>{item.key_date}</td>
              <td>
                <DatePicker
                  selected={item.deadline}
                  onChange={(date) => handleDeadline(date, index)}
                  dateFormat="MM/dd/yyyy"
                  onKeyDown={(e) => {
                    if(item.hasDeadline){
                      e.preventDefault();
                    }
                 }}
                 disabled={item.complete_date}
                />
              </td>
              <td>{ item.notes.length > 0 && item.last_note !== "" ? 
                <p>{item.notes[ item.notes.length -1]} <FontAwesomeIcon icon={faCircleDown} onClick={() => handleListModalVisibility(item.id)} /></p>
                
               : <p>{item.last_note} <FontAwesomeIcon icon={faCircleDown} onClick={() => handleListModalVisibility(item.id)} /></p>

                
              }
              </td>
              <td>
                <FontAwesomeIcon icon={faPlusSquare} onClick={() => handleModalVisibility(item.id)} />
               
              </td>
            </tr>
          ))}
        </tbody>
      </Table>}
      <Footer submit={handleSubmit} close={closeWithoutSave} closeOnSave={closeOnSave}/>
      <NotesModal
        show={show}
        handleClose={() => setShow(false)}
        handleSave={handleNotes}
        note={selectedRow ? selectedRow.note : "No note found in selected row"}
      />
      <NotesListModal
        show={showListModal}
        recordID = {selectedRow?.id}
        handleClose={() => setShowListModal(false)}
        notes={selectedRow ? [selectedRow.notes] : ["No Last note found on selected row"]}
      />

      <ConfirmCompleteModal
        show={showConfirmModal}
        handleClose={() => setShowConfirmModal(false)}
        handleSave={handleComplete}
        />
        <ConfirmCompleteGroupModal
          show={openGroupModal}
          data = {selectedGroup}
          handleClose={() => setOpenGroupModal(false)}
          confirm = {handleCompleteGroupConfirmation}
        />
    </>
  );
};

export default DynamicTable;
