import React from "react";
import { ModalFooter } from "react-bootstrap";
import Modal from "react-bootstrap/Modal";

function ConfirmCompleteModal({ show, handleClose, handleSave }) {
  return (
    <Modal
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      show={show}
      onHide={() => {
        handleClose();
      }}
    >
      <Modal.Header closeButton>
        <Modal.Title>Confirm Complete</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <p>
        Are you sure you want to Close this Key Date and ALL Related Tasks?
        </p>
      </Modal.Body>
      <ModalFooter
        >
        <button
            className="btn btn-primary"
            onClick={() => {
            handleClose();
            }}
        >
            Cancel
        </button>
        <button
            className="btn btn-primary"
            onClick={() => {
            handleSave();
            }}
        >
            Confirm
        </button>
        </ModalFooter>
        
    </Modal>
  );
}


export default ConfirmCompleteModal;